.post-list a {
    color: #4a9c54;
    text-decoration: none;
}

.year {
    margin-bottom: 50px;
}

.year .header:hover {
    cursor: pointer;
    color: gray;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.year .header {
    display: -ms-flexbox;
    display: flex;
    font-size: 1.4em;
    line-height: 1.2em;
    font-weight: bold;
}

.header .line {
    display: inline-block;
    margin-top: 17px;
    height: 2px;
    text-shadow: none;
    background-color: #999;
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.year .entries {
    -webkit-transition: max-height 0.5s ease-in-out;
    -o-transition: max-height 0.5s ease-in-out;
    transition: max-height 0.5s ease-in-out;
    margin-left: 25px;
    margin-right: 10px;
    max-height: 1000px;
}

.year.collapse .entries {
    max-height: 0;
    overflow: hidden;
}

.year .header>.button {
    font-size: 0.8em;
    -webkit-transition: -webkit-transform 0.3s ease-in-out;
    transition: -webkit-transform 0.3s ease-in-out;
    -o-transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.year.collapse .header>.button {
    -webkit-transform: rotate(-90deg);
        -ms-transform: rotate(-90deg);
            transform: rotate(-90deg);
}

.year .entry {
    width: 100%;
    border-bottom: #4a9c54 1px dotted;
}

.entry a:hover {
    padding-left: 12px;
    color: green;
    background-color: #efffe0;
}

.entry a {
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    display: block;
    position: relative;
    text-align: left;
    padding: 20px 0 20px 0;
}

.entry a * {
    display: inline-block;
    margin: 0;
}

.entry a>.entry-title {
    margin-right: 10px;
}

.entry a>time {
    font-size: 0.8em;
    color: #6e6e6e;
}

.entry a>time::before {
    content: "/";
    margin-right: 10px;
}


